import {
  firstIntegration,
  secondIntegration,
  thirdIntegration,
  fourthIntegration,
  testImageIntegration,
} from "@/assets/images";
import { HomeCrumb } from "@/components/bread-crumb";
import { Container } from "@/components/container";
import { InfoHeader } from "@/components/div";
import { Section } from "@/components/section";
import { InfoTitle } from "@/components/title";
import Image from "next/image";
import React from "react";

const services = [
  {
    title: "ПРОЕКТИРОВАНИЕ",
    description:
      "Компания НАГ выполняет полный комплекс проектно-изыскательских работ в области строительства объектов связи и центров обработки данных: от общих идей и полноценных концептов до формирования комплектов исполнительной и эксплуатационной документации. В ходе проектирования специалисты компании плотно взаимодействуют с заказчиком. Учитываются и тщательно прорабатываются тысячи требований для комфортной, эффективной и безопасной работы служб эксплуатации.",
    image: firstIntegration,
  },
  {
    title: "СТРОИТЕЛЬНО-МОНТАЖНЫЕ РАБОТЫ",
    description:
      "Специалисты НАГ имеют большой опыт в реализации комплекса строительно-монтажных работ полного цикла, включая сдачу в эксплуатацию.",
    image: secondIntegration,
  },
  {
    title: "ПУСКОНАЛАДОЧНЫЕ РАБОТЫ",
    description:
      "Пусконаладочные работы от НАГ — комплекс мероприятий, включающий проверку, настройку и тестирование оборудования для последующей передачи в эксплуатацию.",
    image: thirdIntegration,
  },
  {
    title: "АУДИТ ИНФРАСТРУКТУРЫ",
    description:
      "Компания НАГ выполняет независимую экспертизу инфраструктуры с последующей подготовкой отчётов о её техническом состоянии.",
    image: fourthIntegration,
  },
];

const SystemIntegration = () => {
  return (
    <Container>
      <HomeCrumb paths={[{ name: "Системная интеграция от НАГ" }]} />
      <Section className="shadow-md mb-12">
        <InfoHeader className="mb-5">
          <InfoTitle>Системная интеграция от НАГ</InfoTitle>
        </InfoHeader>
        <section className="p-6">
          <Image src={testImageIntegration} alt="pic" className="h-[222px]" />
          <div className="border border-superSilver rounded-lg bg-white mt-4">
            <table className="w-full border-collapse">
              <tbody>
                {services.map((service, index) => (
                  <tr key={index} className="border-b border-superSilver">
                    <td className=" border-r border-superSilver w-[198px] h-[198px] flex items-center justify-center">
                      <Image
                        src={service.image}
                        className="w-[150px] h-[150px]"
                        alt={service.title}
                      />
                    </td>
                    <td className="align-top ">
                      <div className="border-b py-[21px] px-2">
                        <p className="text-[18px] font-normal text-textColor ">
                          {service.title}
                        </p>
                      </div>
                      <p className="text-textColor text-sm pl-2 pr-7 py-5">
                        {service.description}
                      </p>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </section>
      </Section>
    </Container>
  );
};

export default SystemIntegration;
